<template>
    <div>
        <article class="passJoinBox certificationBox">
            <div class="topBox">
                <h1 class="title">
                    Pass by KT <span>금융사기방지 가입</span>
                </h1>
                <div class="con1">
                    <p class="text1">SK planet DAN & KT 제휴</p>
                    <p class="text2">
                        PASS
                        <span>세이프가드</span>로
                        <br />나의 금융거래를
                        <span>
                        더 안전하게<font>!</font>
                        </span>
                    </p>
                </div><!-- con1 end -->
				<!--====================================================== step 2 ===============================================================-->
				<div class="step2">
					<div class="sec2">
						<p class="remark1">
							* 서비스 이용시 휴대폰인증이 필요합니다.
						</p>
						<div class="inputWrap binputWrap">
							<input type="text" placeholder="인증번호 입력" v-model="keyNumber" maxlength="6" >
							<div class="timer">
								<span>{{ timeValue}}</span>
							</div>
						</div>
						<span class="resubmit" @click="reSubmit()">재전송</span>
					</div>
				</div>
				<!--====================================================== step 2 end ===========================================================-->
                <article class="cFooter cFooter2">
                    <div class="btnWrap btnWrap2 cb">
						<div class="left left2">이전</div>
						<div class="right right2 kt">확인</div>
