                        * 본 서비스는 PASS앱(무료)을 설치한 KT 고객만 이용할 수 있습니다. <br>
                        * 문의 : 핀브릿지 고객센터 (1688-4102) <br>
                        * 서비스 해지 : <a href="https://epass.finbridge.co.kr:39443/ew/lgCancel">https://epass.finbridge.co.kr/ew/lgCance</a>
