<script>
export default {
	data() {
		return {
			// 타이머 변수
			keyNumber: '',
			timeValue: '3:00',
		};
	},
	methods: {
		// 휴대폰인증 타이머
		startTimer() {
			this.keyNumber = '';

			//타이머 시간
			this.timeValue = '3:00';
			//3분 => 60*3
			var timer = 180,
				minutes,
				seconds;
			var vd = this;
			this.porling = setInterval(function() {
				minutes = parseInt(timer / 60, 10);
				seconds = parseInt(timer % 60, 10);

				minutes = minutes < 10 ? minutes : minutes;
				seconds = seconds < 10 ? '0' + seconds : seconds;

				vd.timeValue = minutes + ':' + seconds;

				if (--timer < 0) {
					timer = 0;
				}
			}, 1000);
		},
		// 재전송 버튼
		reSubmit() {
			// 재전송 버튼 클릭시 타이머 작동 초기화
			clearInterval(this.porling);
			this.startTimer();
		},
	},
	created(){
		this.startTimer();
	}
};
</script>

